cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        var self = this
        var share_bg = this.node.getChildByName("share_bg")//cc.find('Canvas/record_layer/record_bg');
        var callback = function(){
            self.node.active = false;
        }
        wsft.uiFuncs.bindTouchLayer(this.node,share_bg,callback);
        wsft.message.on(wsft.protocol.adaptiveView,this.showShare.bind(this));
    },

    start () {
        this.showShare()
    },
    showShare:function(){
        // var 
        var share_1 = this.node.getChildByName("share_1")
        var share_2 = this.node.getChildByName("share_2")
        if( wsft.adaptiveView.ORTRAIT == 1){//竖屏
            share_1.active = true
            share_2.active = false
        }else{
            share_1.active = false
            share_2.active = true
        }
    },
    // update (dt) {},
});
